<template>
  <div class="wrapC">
    <img v-bind:src="user.image" alt />
  </div>
</template>

<script>
const axios = require("axios");

export default {
  data: () => {
    return {
      email: "",
      user: {
        nickname: "",
        intro: "",
        image: ""
      }
    };
  },
  mounted() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      //   this.email = this.$router.params.email;
      this.email = "aa@aa.com";
      axios
        .post("http://localhost:8080/user/myPage", null, {
          params: {
            useremail: this.email
          }
        })
        .then(res => {
          console.log(res);
          this.user.image = res.data.object.image;
          this.user.intro = res.data.object.intro;
          this.user.nickname = res.data.object.nickname;
        });
    }
  }
};
</script>
