import Login from "./views/user/Login.vue";
import Join from "./views/user/Join.vue";
import Components from "./views/Components.vue";
import InputEmail from "./views/user/InputEmail.vue";
import CompleteJoin from "./views/user/CompleteJoin.vue";
import SendEmail from "./views/user/SendEmail.vue";
import PageNotFound from "./views/PageNotFound.vue";
import Mypage from "./views/user/Mypage";

export default [
  {
    path: "/",
    name: "Login",
    component: Login
  },
  {
    path: "/user/join",
    name: "Join",
    component: Join
  },
  {
    path: "/components",
    name: "Components",
    component: Components
  },
  {
    path: "/user/inputemail",
    name: "InputEmail",
    component: InputEmail
  },
  {
    path: "/user/completejoin",
    name: "CompleteJoin",
    component: CompleteJoin
  },
  {
    path: "/user/sendemail",
    name: "SendEmail",
    component: SendEmail
  },
  {
    path: "/user/mypage",
    name: "Mypage",
    component: Mypage
  },
  {
    path: "*",
    redirect: "/404"
  },
  {
    path: "/404",
    name: "PageNotFound",
    component: PageNotFound
  }
];
